:root {
  --pink: #725861;
  --pink-dark: #e9dee2;
  --card-bg: #ffffffee;
  --bg: #f5f5f5;
  --ink: #222;
}

* {
  box-sizing: border-box;
}

.participants-wrap {
  min-height: 100vh;
  padding: 40px 16px;
  background-color: black;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.participants-title {
  text-align: center;
  font-size: 2.2rem;
  color: var(--pink-dark);
  margin: 0 0 10px;
}

.participants-subtitle {
  text-align: center;
  color: black;
  margin-bottom: 40px;
}

.participants-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
  max-width: 1000px;
  margin: 0 auto;
}

.participant-card {
  background: var(--card-bg);
  padding: 20px;
  border-radius: 16px;
  text-align: center;
  box-shadow: 0 4px 10px rgba(0,0,0,0.08);
  transition: transform 0.2s ease;
}

.participant-card:hover {
  transform: translateY(-5px);
}

.participant-image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid var(--pink);
  margin-bottom: 14px;
}

.participant-card h3 {
  margin: 0;
  font-size: 1.3rem;
  color: var(--ink);
}

.participant-card p {
  margin: 8px 0 0;
  color: black;
  font-size: 0.95rem;
}
